import axios from '@nextcloud/axios';
import Tooltip from '@nextcloud/vue/dist/Directives/Tooltip';
import { showError } from '@nextcloud/dialogs';
import Action from './Action';
import { generateOcsUrl } from '@nextcloud/router';
import moment from '@nextcloud/moment';
import RichText from '@juliushaertl/vue-richtext';
import DefaultParameter from './Parameters/DefaultParameter';
import File from './Parameters/File';
import User from './Parameters/User';
export default {
  name: 'Notification',
  components: {
    Action: Action,
    RichText: RichText
  },
  directives: {
    tooltip: Tooltip
  },
  props: {
    notificationId: {
      type: Number,
      default: -1,
      required: true
    },
    datetime: {
      type: String,
      default: '',
      required: true
    },
    app: {
      type: String,
      default: '',
      required: true
    },
    icon: {
      type: String,
      default: '',
      required: true
    },
    link: {
      type: String,
      default: '',
      required: true
    },
    user: {
      type: String,
      default: '',
      required: true
    },
    message: {
      type: String,
      default: '',
      required: true
    },
    messageRich: {
      type: String,
      default: '',
      required: true
    },
    messageRichParameters: {
      type: [Object, Array],
      default: function _default() {
        return {};
      },
      required: true
    },
    subject: {
      type: String,
      default: '',
      required: true
    },
    subjectRich: {
      type: String,
      default: '',
      required: true
    },
    subjectRichParameters: {
      type: [Object, Array],
      default: function _default() {
        return {};
      },
      required: true
    },
    objectType: {
      type: String,
      default: '',
      required: true
    },
    objectId: {
      type: String,
      default: '',
      required: true
    },
    actions: {
      type: Array,
      default: function _default() {
        return [];
      },
      required: true
    },
    index: {
      type: Number,
      default: -1,
      required: true
    }
  },
  data: function data() {
    return {
      showFullMessage: false
    };
  },
  _$el: null,
  computed: {
    timestamp: function timestamp() {
      return new Date(this.datetime).valueOf();
    },
    absoluteDate: function absoluteDate() {
      return moment(this.timestamp).format('LLL');
    },
    relativeDate: function relativeDate() {
      var diff = moment().diff(moment(this.timestamp));

      if (diff >= 0 && diff < 45000) {
        return t('core', 'seconds ago');
      }

      return moment(this.timestamp).fromNow();
    },
    useLink: function useLink() {
      if (!this.link) {
        return false;
      }

      var parametersHaveLink = false;
      Object.keys(this.subjectRichParameters).forEach(function (p) {
        if (p.link) {
          parametersHaveLink = true;
        }
      });
      return !parametersHaveLink;
    },
    preparedSubjectParameters: function preparedSubjectParameters() {
      return this.prepareParameters(this.subjectRichParameters);
    },
    preparedMessageParameters: function preparedMessageParameters() {
      return this.prepareParameters(this.messageRichParameters);
    },
    isCollapsedMessage: function isCollapsedMessage() {
      return this.message.length > 200 && !this.showFullMessage;
    }
  },
  mounted: function mounted() {
    this._$el = $(this.$el); // Parents: TransitionGroup > NotificationsList

    if (this.$parent.$parent.showBrowserNotifications) {
      this._createWebNotification();
    }
  },
  methods: {
    prepareParameters: function prepareParameters(parameters) {
      var richParameters = {};
      Object.keys(parameters).forEach(function (p) {
        var type = parameters[p].type;

        if (type === 'user') {
          richParameters[p] = {
            component: User,
            props: parameters[p]
          };
        } else if (type === 'file') {
          richParameters[p] = {
            component: File,
            props: parameters[p]
          };
        } else {
          richParameters[p] = {
            component: DefaultParameter,
            props: parameters[p]
          };
        }
      });
      return richParameters;
    },
    onClickMessage: function onClickMessage(e) {
      if (e.target.classList.contains('rich-text--wrapper')) {
        // Vue RichText
        this.showFullMessage = !this.showFullMessage;
      } else if (!this.messageRich && !!this.message) {
        // Plain text
        this.showFullMessage = !this.showFullMessage;
      }
    },
    onDismissNotification: function onDismissNotification() {
      var _this = this;

      axios.delete(generateOcsUrl('apps/notifications/api/v2', 2) + 'notifications/' + this.notificationId).then(function () {
        _this.$emit('remove', _this.index);
      }).catch(function () {
        showError(t('notifications', 'Failed to dismiss notification'));
      });
    },

    /**
     * Create a browser notification
     * @see https://developer.mozilla.org/en/docs/Web/API/notification
     */
    _createWebNotification: function _createWebNotification() {
      var n = new Notification(this.subject, {
        title: this.subject,
        lang: OC.getLocale(),
        body: this.message,
        icon: this.icon,
        tag: this.notificationId
      });

      if (this.link) {
        n.onclick = function (event) {
          event.preventDefault();
          window.location.href = this.link;
        }.bind(this);
      }

      setTimeout(n.close.bind(n), 5000);
    }
  }
};